@font-face {
  font-family: 'd-movie';
  src:
    url('../../font/Icon/d-movie.ttf?8l6ayn') format('truetype'),
    url('../../font/Icon/d-movie.woff?8l6ayn') format('woff'),
    url('../../font/Icon/d-movie.svg?8l6ayn#d-movie') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'd-movie' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-logo:before {
  content: "\e904";
}
.icon-close:before {
  content: "\e900";
}
.icon-star-outline:before {
  content: "\e903";
}
.icon-star-rate:before {
  content: "\e902";
}
.icon-search:before {
  content: "\e901";
}
